.alert {
    width: 300px;
    height: 50px;
    background-color: rgb(207, 67, 67);
    color: #fff;
    border-radius: 2px;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 20px;
    left: 20px;
  }
  
  .enter {
    opacity: 0;
    transform: translateX(-100%);
  }
  
  .enterActive {
    opacity: 0.8;
    transform: translateX(0);
    transition: all 250ms linear;
  }
  
  .exit {
    opacity: 0.8;
    transform: translateX(0);
  }
  
  .exitActive {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 250ms linear;
  }